/* mixins*/

@color: #dcb9b9;
@shadowone:10px 10px 10px #888888;

.box-shadow (@string) {
	-webkit-box-shadow: @string;
	-moz-box-shadow:    @string;
	box-shadow:         @string;
}
.border-radius (@radius: 5px) {
	-webkit-border-radius: @radius;
	-moz-border-radius:    @radius;
	border-radius:         @radius;
}
.opacity (@opacity: 0.5) {
	-webkit-opacity: 	@opacity;
	-moz-opacity: 		@opacity;
	opacity: 		@opacity;
}

/* base starts*/

.container {
	h1,h2,h3,h4,h5,h6,ul,li,a{
	padding:0;
	margin:0;
	font-size:12px;
	color:#000;
	}
	ul li{
		display:block;
		
	}
}
.nize{
	font-size: 12px;
}


/* base ends*/

/* section one starts*/
.box{
	 .box-shadow (@shadowone) ;    /* effect one*/
	    .opacity(1);
	    .border-radius();
}
.sect{
	h1{
	    font-size: 28px;
	    text-align: center;
	    text-transform: uppercase;
	    font-weight: bold;
	    padding: 2% 0;
	    color: #000;
	}
	.butn{
		padding: 6px 0;
	    width: 200px;
	    font-size: 22px;
	    margin: 0 auto;
	    display: block;
	    color:@color;
	    .box;

	}
	.secthide{
	    margin-top: 28px;
	    background: #dcc7c7;
	    padding: 37px 0;
	    text-align: center;
	    font-size: 20px;
	    text-transform: uppercase;
	    letter-spacing: 10px;
	    text-indent:2px;
	    .box;

	}

}

.sectpad{
	position: absolute;
	top: 0;
	z-index: -10;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
}
.fd{
	padding:10% 0;
	overflow:hidden;
}
.secthide{
	background: #000;

}

/* section one ends*/